    "": "不计费",
    0: "按需计费",
    1: "包年包月"
  };
  private billing_method_list = [];
  private ecs_goods_name_list = [];
  private timer = null;
  private os_info = {};
  private ecs_info = {};
  private common_visible:boolean=false
